  display: none;
}
/* User interaction
 * ========================================================================== */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* base font settings */
html {
  font-size: 62.5%;
}

body {
  font-size: 1.5em;
  line-height: 1.6;
  font-weight: 400;
  font-family: "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #222;
}

/* clean up titles  */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-weight: 300;
}
h1 {
  font-size: 4rem;
  line-height: 1.2;
  letter-spacing: -0.1rem;
}
h2 {
  font-size: 3.6rem;
  line-height: 1.25;
  letter-spacing: -0.1rem;
}
h3 {
  font-size: 3rem;
  line-height: 1.3;
  letter-spacing: -0.1rem;
}
h4 {
  font-size: 2.4rem;
  line-height: 1.35;
  letter-spacing: -0.08rem;
}
h5 {
  font-size: 1.8rem;
  line-height: 1.2;
  letter-spacing: 0.55rem;
}
h6 {
  font-size: 1.5rem;
  line-height: 1.6;
  letter-spacing: 0;
}

/* Larger than mobile */
@media (min-width: 550px) {
  h1 {
    font-size: 5rem;
  }
  h2 {
    font-size: 4.2rem;
  }
  h3 {
    font-size: 3.6rem;
  }
  h4 {
    font-size: 3rem;
  }
  h5 {
    font-size: 2.4rem;
  }
  h6 {
    font-size: 1.5rem;
  }
}

/* set up images */
img {
  width: 100%;
  display: block;
  vertical-align: bottom;
}

header {
  font-family: "Bangers", Arial Black;
  margin: 10px 0;
  text-align: center;
}

.details {
  background-color: white;
  border-radius: 2px;
  border: 1px solid #bbb;
  box-shadow: 2px 2px 5px #eee;
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100%;
  left: 0px;
  overflow-y: auto;
  padding: 0 10px 10px 10px;
  position: fixed;
  top: 0px;
  transition: -webkit-transform 800ms ease-in-out;
  transition: transform 800ms ease-in-out;
  transition: transform 800ms ease-in-out, -webkit-transform 800ms ease-in-out;
}

/* animates the details view to slide in... */
.details.hidden-on-mobile {
  -webkit-transform: translate(-600px);
          transform: translate(-600px);
  display: inline-block;
}
/* ... and out */
.details.visible-on-mobile {
  -webkit-transform: translate(0px);
          transform: translate(0px);
  display: inline-block;
}

.close-details {
  background-color: red;
  border: 1px solid black;
  color: white;
  cursor: pointer;
  font-family: "Bangers", Arial Black;
  font-size: 30px;
  font-weight: bold;
  height: 44px;
  position: fixed;
  right: -13px;
  text-align: center;
  top: 55%;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  vertical-align: middle;
  width: 92px;
  z-index: 20;
}

.hidden-on-mobile {
  display: none;
}

.detail-text {
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 10px 0 0;
}

.detail-title {
  margin: 3px;
  font-size: 1.5em;
  font-family: "Bangers", Arial Black;
  letter-spacing: 0.25rem;
}

.primary-button {
  background-color: red;
  border-radius: 5px;
  color: white;
  display: inline-block;
  font-family: "Bangers", Arial Black;
  font-size: 2em;
  margin: 10px 0;
  padding: 0.05em 0;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;
}

/* Larger than mobile */
@media (min-width: 550px) {
  .details {
    background-color: white;
    border-radius: 2px;
    border: 1px solid #bbb;
    box-shadow: 2px 2px 5px #eee;
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 100%;
    left: 0px;
    overflow-y: auto;
    padding: 0 10px 10px 10px;
    position: relative;
    top: 0px;
    transition: -webkit-transform 800ms ease-in-out;
    transition: transform 800ms ease-in-out;
    transition: transform 800ms ease-in-out, -webkit-transform 800ms ease-in-out;
  }

  /* keine animation intablet und desktop */
  .details.hidden-on-mobile,
  .details.visible-on-mobile {
    -webkit-transform: translate(0px);
            transform: translate(0px);
    display: inline-block;
  }

  .hidden-on-mobile {
    display: inline-block;
  }

  .hidden-on-gt-mobile {
    display: none;
  }
}

/* Larger than tablet */
@media (min-width: 850px) {
  .details {
    background-color: white;
    border-radius: 2px;
    border: 1px solid #bbb;
    box-shadow: 2px 2px 5px #eee;
    -webkit-flex: 2 1;
            flex: 2 1;
    height: 100%;
    left: 0px;
    overflow-y: auto;
    padding: 0 10px 10px 10px;
    position: relative;
    top: 0px;
    transition: -webkit-transform 800ms ease-in-out;
    transition: transform 800ms ease-in-out;
    transition: transform 800ms ease-in-out, -webkit-transform 800ms ease-in-out;
  }

  .detail-cover-text-container {
    display: -webkit-flex;
    display: flex;
  }

  .detail-cover {
    -webkit-flex: 1 1;
            flex: 1 1;
    border-radius: 2px;
    margin-bottom: 15px;
  }

  .detail-text {
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 0 0 0 13px;
  }
}

.list-item {
  border-radius: 2px;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 5px #eee;
  box-sizing: border-box;
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 10px;
}

.list-item-cover {
  -webkit-flex: 1 1;
          flex: 1 1;
  border-radius: 2px;
}

.list-item-description {
  -webkit-flex: 2 1;
          flex: 2 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 3px 3px 0 10px;
}

/* only show active and hover effects
  in the master/detail view */
@media (min-width: 550px) {
  .list-item.active,
  .list-item:hover {
    background-color: #eee;
  }
}

.comics-list {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100%;
  margin-right: 10px;
  overflow-y: auto;
}

.random-button {
  background-color: red;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  display: inline-block;
  font-family: "Bangers", Arial Black;
  font-size: 2em;
  margin-bottom: 10px;
  padding: 0.05em 0;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;
}

main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  overflow-y: hidden;
}

/* setup the container for mobile... */
.container {
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
  margin: 0 auto;
  max-width: 960px;
  padding: 0 20px;
  position: relative;
  width: 100%;
}

/* ...and for devices larger than mobile */
@media (min-width: 550px) {
  .container {
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
    margin: 0 auto;
    max-width: 960px;
    padding: 0;
    position: relative;
    width: 85%;
  }
}

/* a loading wrapper, that contains a title and an animated spinner */
.loading-wrapper {
  text-align: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.loading-title {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: auto;
}

.loading-ripple {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-top: 30px;
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
  margin: 10px auto;
}
.loading-ripple div {
  position: absolute;
  border: 4px solid #aaa;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: loading-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: loading-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.loading-ripple div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}

@-webkit-keyframes loading-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}

@keyframes loading-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}

